import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'estadoPedidos'
})
export class EstadoPedidosPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    switch (value) {
      case 'NUEVO': {
        return 'Cliente Nuevo';
      }
      case 'CREAD': {
        return 'Creado';
      }
      case 'ELIMI': {
        return 'Eliminado';
      }
      case 'ENPRO': {
        return 'En Produccin';
      }
      case 'FACTU': {
        return 'Facturado';
      }
      case 'MAREC': {
        return 'Mal recepcionado';
      }
      case 'NOPRO': {
        return 'No Despachado';
      }
   }
  }

}
