import { Component, OnInit, enableProdMode } from '@angular/core';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { SettingsService } from '../../../core/settings/settings.service';
import * as _ from 'lodash';
import { FormGroup, FormBuilder, FormControl, NgForm } from '@angular/forms';
import { NgxSpinnerService } from 'ngx-spinner';
import { AuthService } from '../../../core/consultaService/auth.service';
import { ConsultasService } from './../../../core/consultaService/consultas.service';
import { LoginService } from './../../../core/consultaService/login.service';
import { ArticuloService } from '../../../core/consultaService/articulo.service';
import { Router } from '@angular/router';
import { Md5 } from "md5-typescript";
import { ExistenciasService } from '../../../core/consultaService/existencias.service';

const swal = require('sweetalert');

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss']
})

export class LoginComponent implements OnInit {

  request: any = {};

  forma: FormGroup;
  user: any = {};
  activaSucursal: boolean;
  primeraSession:boolean;
  textoBuscaSucu:boolean;
  parametrosArticulo: any = {};
  parametrosCliente: any = {};
  parametrosTipoDescuento: any = {};
  parametrosTipoEstructura: any = {};
  articuloAutocomplete: any = {};
  parametrosIvaIaba: any = {};
  parametrosTipoMovimiento: any = {};
  bancos: any = {};
  motivoProtesto: any = {};
  usuario: string = '';
  clave: string = '';
  rutOperador: string = '';
  pass: string = '';
  email: string = '';
  user_string: any = {};
  sucursales: any = {};
  sucursal: any = {};
 
 
 
  /*            toUpper(pass: string) { this.pass = ''; }*/

  constructor(public settings: SettingsService, fb: FormBuilder, public http: HttpClient,
    private userService: AuthService, public cargador: NgxSpinnerService,
    private router: Router, private consultasService: ConsultasService,
    private articuloService: ArticuloService, private existencia: ExistenciasService,
    private loginService: LoginService) {

    this.forma = new FormGroup({
      email: new FormControl(),
      pass: new FormControl(),
      sucursal: new FormControl({ value:'', disabled: true }),
    });
  }

  ngOnInit() {
    this.textoBuscaSucu= false;
    // this.primeraSession=true;
    this.sucursales=[];
    this.request = {
      scodaplicacion: 'NES',
      scontrasena: this.forma.value.pass,
      snomusuario: this.forma.value.email
    }
    this.forma.controls.sucursal.setValue('Seleccione Sucursal');
  }

  entrar($event: any) {
      this.textoBuscaSucu =false;
    // if(this.primeraSession==true){    
      this.pass = Md5.init(this.forma.value.pass).toUpperCase();
      this.cargador.show();
      this.request = {
        scodaplicacion: 'NES',
        scontrasena: this.pass,
        snomusuario: this.forma.value.email
      }
  
      this.loginService.getValidaLogeo(this.request)
        .subscribe((data: any) => {
          this.cargador.hide();
          if (data.validaLogeo) {
            this.user.clave = this.request.scontrasena;
            this.user.sidsesion = data.validaLogeo[0].ses_sidsesion;
            this.user.dfhologueo = data.validaLogeo[0].ses_dfhologueo;
            this.user.dfhoultimaconsulta = data.validaLogeo[0].ses_dfhoultimaconsulta;
            this.user.scodaplicacion = data.validaLogeo[0].ses_scodaplicacion;
            this.user.scodrol = data.validaLogeo[0].ses_scodrol;
            this.user.snomusuario = data.validaLogeo[0].ses_snomusuario;
            this.user.nrunusuario = data.validaLogeo[0].ses_nrunusuario;
            this.user.snombre = data.validaLogeo[0].ses_snombre;
            this.user.sapepaternousuario = data.validaLogeo[0].ses_sapepaternousuario;
            this.user.sapematernousuario = data.validaLogeo[0].ses_sapematernousuario;
            this.user.nrutoperador = data.validaLogeo[0].ses_nrutoperador;
            this.user.snombreoperador = data.validaLogeo[0].ses_snombreoperador;
            this.user.sdata01 = data.validaLogeo[0].ses_sdata01;
            this.user.sdata02 = data.validaLogeo[0].ses_sdata02;
            this.user.sdata03 = data.validaLogeo[0].ses_sdata03;
            this.user.sdata04 = data.validaLogeo[0].ses_sdata04;
            this.user.sdata05 = data.validaLogeo[0].ses_sdata05;
            this.user.sindexpira = data.validaLogeo[0].sindexpira;
  
            this.userService.setUser(this.user);
            this.textoBuscaSucu = true;
            swal('Atencin', 'Seleccione una sucursal para ingresar.', 'success');  
            if (data.validaLogeo.length == 0 && data.error.codError == 0) {
              swal('Atencin', 'No se encontraron resultados', 'info');
              this.cargador.hide();
            } else if (data.error.codError == 0 && data.error.msjError != ' ') {
              this.textoBuscaSucu =false;
              swal('Atencin', 'Usuario y contrasea no valido!', 'error');              
              this.cargador.hide();
              this.textoBuscaSucu =false;
            } else {
              this.consultasService.getParametrosArticulo()
                .subscribe((data: any) => {
                  this.parametrosArticulo = data.general;
                  this.userService.setParametrosArticulo(this.parametrosArticulo);
                });
              this.consultasService.getParametrosCliente()
                .subscribe((data: any) => {
                  this.parametrosCliente = data.general;
                  this.userService.setParametrosCliente(this.parametrosCliente);
                });
  
              // Request getParametroTipoDescuento
              this.request.parcodparametro01 = '';
              this.request.parcodparametro02 = '';
              this.request.parcodparametro03 = '';
              this.request.parcodparametro04 = '';
              this.request.parcodparametro05 = '';
              this.request.parnumparametro = 17;
  
              this.consultasService.getParametrosTipoDescuento(this.request)
                .subscribe((data: any) => {
                  this.parametrosTipoDescuento = data.general;
                  this.userService.setParametrosTipoDescuento(this.parametrosTipoDescuento);
                }),
                error => {
                  this.cargador.hide();
                  swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
                }  
              // Request getParametroTipoEstructura
              this.request.parcodparametro01 = '';
              this.request.parcodparametro02 = '';
              this.request.parcodparametro03 = '';
              this.request.parcodparametro04 = '';
              this.request.parcodparametro05 = '';
              this.request.parnumparametro = 62;
  
              this.consultasService.getParametrosTipoEstructura(this.request)
                .subscribe((data: any) => {
                  this.parametrosTipoEstructura = data.general;
                  this.userService.setParametrosTipoEstructura(this.parametrosTipoEstructura);
                }),
                error => {
                  this.cargador.hide();
                  swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
                }
              //Request getArticuloAutocomplete
              this.request.rutOperador = this.user.nrutoperador;
              this.articuloService.getArticuloAutocomplete(this.request)
                .subscribe((data: any) => {
                  this.articuloAutocomplete = data.articulo;
                  this.userService.setArticuloAutocomplete(this.articuloAutocomplete);
                }),
                error => {
                  this.cargador.hide();
                  swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
                }
  
              //Request getParametrosBancos
              this.request.parcodparametro01 = '';
              this.request.parcodparametro02 = '';
              this.request.parcodparametro03 = '';
              this.request.parcodparametro04 = '';
              this.request.parcodparametro05 = '';
              this.request.parnumparametro = 20;
              this.consultasService.getParametros(this.request)
                .subscribe((data: any) => {
                  this.bancos = data.general;
                  this.userService.setParametrosBancos(this.bancos);
                  },
                  error => {
                    swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
                  });
  
              //Request getParametrosIvaIaba
              this.request.parcodparametro01 = '';
              this.request.parcodparametro02 = '';
              this.request.parcodparametro03 = '';
              this.request.parcodparametro04 = '';
              this.request.parcodparametro05 = '';
              this.request.parnumparametro = 16;
              this.consultasService.getParametros(this.request)
                .subscribe((data: any) => {
                  this.parametrosIvaIaba = data.general;
                  this.userService.setParametrosIvaIaba(this.parametrosIvaIaba);
                },
                  error => {
                    swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
                  });
  
              // Request getParametroTipoMovimiento
              this.request.parcodparametro01 = '';
              this.request.parcodparametro02 = '';
              this.request.parcodparametro03 = '';
              this.request.parcodparametro04 = '';
              this.request.parcodparametro05 = '';
              this.request.parnumparametro = 35;
  
              this.consultasService.getParametros(this.request)
                .subscribe((data: any) => {
                  this.parametrosTipoMovimiento = data.general;
                  this.userService.setParametrosTipoMovimiento(this.parametrosTipoMovimiento);
                },
                  error => {
                    this.cargador.hide();
                    swal('Atencin', 'Ha ocurrido un error inesperado!', error);
                  });  
              //Request getParametrosMotivoProtesto
              this.request.parcodparametro01 = '';
              this.request.parcodparametro02 = '';
              this.request.parcodparametro03 = '';
              this.request.parcodparametro04 = '';
              this.request.parcodparametro05 = '';
              this.request.parnumparametro = 36;
              this.consultasService.getParametros(this.request)
                .subscribe((data: any) => {
                  this.buscaSucursal();
                  this.motivoProtesto = data.general;
                  this.forma.controls.sucursal.enable();
                  this.userService.setParametrosMotivoProtesto(this.motivoProtesto);
                  this.cargador.hide();                
                },
                  error => {
                    swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
                  });
  
            }
            if (data.error.codError != 0 && data.error.msjError != ' ') {
              this.cargador.hide();
              swal('Atencin', 'Usuario y contrasea no valido!', 'error');
            }
          } else {
            this.cargador.hide();
            swal('Atencin', 'Usuario y contrasea no valido!', 'error');
          }
      
  
        }),
        error => {
          this.cargador.hide();
          swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
        }

  }

  buscaSucursal(){
    this.sucursales=[];
    console.log('entra a buscar sucursal'); 
    this.request={}
    this.request.nRunUsuario= this.user.nrunusuario;
    this.request.nRutOperador= this.user.nrutoperador;

    this.existencia.getSucursalesExistenciaLogeo(this.request)
    .subscribe((data: any) => {
      if(data){
          for(let i in data.sucursallog){
            let sucursal={	
              sus_scodsucursal: data.sucursallog[i].sus_scodsucursal,												
							suc_sdescsucursal: data.sucursallog[i].suc_sdescsucursal,	
            }	
            this.sucursales.push(sucursal);
          }     
      }else{
        swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
      }       
    },
      error => {
        this.cargador.hide();
        swal('Atencin', 'Ha ocurrido un error inesperado!', 'error');
      });
  }
  				
  seleccionaSucursal(){
    this.sucursal = this.forma.controls.sucursal.value;
    if(this.sucursal === undefined){
      swal('Atencin', 'Debe seleccionar una sucursal', 'error');
    }else{
      this.sucursal = this.forma.controls.sucursal.value;
      let sucursalUsu = JSON.stringify(this.sucursal);
      localStorage.setItem("SucursalUsuario", sucursalUsu);

      this.sucursales=[];
      this.router.navigate(['/home']); 
    }
  }
}  

