import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'dias'
})
export class DiasPipe implements PipeTransform {

  transform(value: any, args?: any): any {

    if (value === 'LUNES') {
      return 'Lunes';
    } else if (value === 'MARTE') {
        return 'Martes';
    } else if (value === 'MIERC') {
        return 'Miercoles';
    } else if (value === 'JUEVE') {
        return 'Jueves';
    } else if (value === 'VIERN') {
        return 'Viernes';
    } else if (value === 'SABAD') {
        return 'Sabado';
    } else {
        return 'Domingo';
    }
  }
}