    let headerRow2 = worksheet.addRow(['', '', 'T1 : 1 - 5', 'T2 : 6 - 15', 'T3 : 16 - 30', 'T4 : 31 - 45','T5 : 46 - 60','T6 : 61 - 120','T7 : 121 - 180','T8 : 181 y mas','Total Vencido','Total','Dias Cartera']);
