    auxData2.push(parametros.totalDivisional);
    auxData3.push(parametros.totalCorporativo + parametros.totalDivisional);

    auxData1.push(parametros.totalCorporativo + parametros.nMontoPorVencerCor);
    auxData2.push(parametros.totalDivisional + parametros.nMontoPorVencerDiv);
    auxData3.push(parametros.totalGeneral);

