	constructor(private http: HttpClient) { }
	
	getArticulos(request:any){
	    // add headers
	    let headers = new HttpHeaders({
	      'Content-Type': 'application/json',
	      'Accept': '*/*'
	    });
	    // get articulos from api
	    return this.http.post(this.baseUrl + "articulo/busca/administracion",request, {headers});
	}
	
