import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'estadoVigencia'
})
export class EstadoVigenciaPipe implements PipeTransform {

  transform(value: any, args?: any): any {

    if(value === 'VIGEN'){
      return 'Vigente';
    }
    else{
      return 'No Vigente';
    }
  }
}
