import { Pipe, PipeTransform } from '@angular/core';

@Pipe({
  name: 'formatoRut'
})
export class FormatoRutPipe implements PipeTransform {

  transform(value: any, args?: any): any {
    if (value !== '') {
      let longitud_rutClientes = value.toString();
      let longitud_rutClientes2 = longitud_rutClientes.length;
      let formatRut = longitud_rutClientes.substr(0, longitud_rutClientes2 - 1).replace(/(\d)(?=(\d{3})+(?!\d))/g, '$1.');
      let formatVerificador = longitud_rutClientes.substr((longitud_rutClientes2 - 1), 1);
      return `${formatRut}-${formatVerificador}`;
    }

  }

}
