import { Injectable } from "@angular/core";
import { HttpClient, HttpHeaders } from "@angular/common/http";
import { Observable } from "rxjs/internal/Observable";
import { map } from "rxjs/operators";
import { isNullOrUndefined } from "util";
import { environment } from './../../../environments/environment';

@Injectable({
	providedIn: 'root'
})
export class LoginService {

	private baseUrl = environment.baseLoginUrl;
	/*console.log(baseUrl);*/

	constructor(private http: HttpClient) {} 

    getValidaLogeo(request:any){
      let headers = new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': '*/*'
      });
      return this.http.post(this.baseUrl + "login/valida/logeo",request, {headers});
    }

    getBuscaSucursalLogueo(request:any){
      let headers = new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': '*/*'
      });
      return this.http.post(this.baseUrl + "login/busca/sucursalLogeo",request, {headers});
    }

    getActualizaContrasena(request:any){
      let headers = new HttpHeaders({
        'Content-Type': 'application/json',
        'Accept': '*/*'
      });
      return this.http.post(this.baseUrl + "login/actualiza/contrasena",request, {headers});
    }


}
