import { LayoutComponent } from '../layout/layout.component';

import { LoginComponent } from './pages/login/login.component';
import { RegisterComponent } from './pages/register/register.component';
import { RecoverComponent } from './pages/recover/recover.component';
import { LockComponent } from './pages/lock/lock.component';
import { MaintenanceComponent } from './pages/maintenance/maintenance.component';
import { Error404Component } from './pages/error404/error404.component';
import { Error500Component } from './pages/error500/error500.component';

export const routes = [

    {
        path: '',
        component: LayoutComponent,
        children: [
            { path: '', redirectTo: 'login', pathMatch: 'full' },
            { path: 'home', loadChildren: './home/home.module#HomeModule' },
            { path: 'buscaCliente', loadChildren: './cliente/cliente.module#ClienteModule' },
            { path: 'pedidos', loadChildren: './pedidos/pedidos.module#PedidosModule' },
            { path: 'existencias', loadChildren: './existencias/existencias.module#ExistenciasModule' },
            { path: 'buscaArticulo', loadChildren: './articulo/articulo.module#ArticuloModule' },
            { path: 'cajaCobranza', loadChildren: './cajaCobranza/cajaCobranza.module#CajaCobranzaModule' },
            { path: 'administracionComercial', loadChildren: './administracionComercial/administracionComercial.module#AdministracionComercialModule' },
            { path: 'administracionOperativa', loadChildren: './administracionOperativa/administracionOperativa.module#AdministracionOperativaModule' },
            { path: 'buscaGestionInversion', loadChildren: './gestionAccionComercial/gestionAccionComercial.module#GestionAccionComercialModule'},
            { path: 'reportes', loadChildren: './reportes/reporte.module#ReporteModule' },
            { path: 'cambiaClave', loadChildren: './cambiaClave/cambiaClave.module#CambiaClaveModule' },
            { path: 'documentos', loadChildren: './documentos/documentos.module#DocumentosModule' },


        ]
    },

    // Not lazy-loaded routes
    { path: 'login', component: LoginComponent },
    { path: 'register', component: RegisterComponent },
    { path: 'recover', component: RecoverComponent },
    { path: 'lock', component: LockComponent },
    { path: 'maintenance', component: MaintenanceComponent },
    { path: '404', component: Error404Component },
    { path: '500', component: Error500Component },

    // Not found
    { path: '**',pathMatch: 'full', redirectTo: 'login' }

];
